@import "../../global.scss";

.navbar{
  width: 100%;
  height: 60px;
  background-color: $mainColor;
  position: fixed;
  top: 0;
  z-index: 2;

  .wrapper{
    padding: 10px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &::-webkit-scrollbar{
      display:none
    }

    .left{
      display: flex;
      align-items: center;

      .logo{
        font-size: 33px;
        font-weight: 700;
        text-decoration: none;
        color: white;
        margin-right: 40px;
      }

      .itemContainer{
        display: flex;
        align-items: center;
        margin-left: 30px;

        .icon{
          font-size: 18px;
          margin-right: 5px;
        }

        span{
          font-size: 15px;
          font-weight: 500;
        }

        .nav-link{
          text-decoration: none;
          font-size: 20px;
          margin: 0px 50px
        }
        .nav-link:hover{
          color: white;
          text-shadow: 1px 1px 2px blue;
        }
      }
      .nav-container{
        padding-left: 15vw;
      }
    }
  }
}